import { Observable } from 'rxjs/Rx';
export class Insertion {
    public view1 = {};

    public b_sort(input: number[]) {
        this.view1 = input; //
        let temp_input = []; //
        let t_input = input; //

        //
        for (let i = 0; i < t_input.length; i++) {
            for (let j = 0; j < t_input.length - 1 - i; j++) {
                if (t_input[j] > t_input[j + 1]) {
                    let temp: number = t_input[j];
                    t_input[j] = t_input[j + 1];
                    t_input[j + 1] = temp;
                    temp_input.push(t_input.slice()); //slice()push
                    //ref:http://stackoverflow.com/questions/7486085/copying-array-by-value-in-javascript/7486130#7486130          
                }
            }
        }

        //
        for (let i = 0; i < temp_input.length; i++) {
            setTimeout(() => {
                //console.log(temp_input[i]);
                this.view1 = temp_input[i];
            }, i * 1000);
        }
    }
}


